<template>
    <div id="app">
        <h2>在线翻译</h2>
        <h5 class="text-muted">简单 / 易用 / 便捷</h5>
        <translateForm v-on:formSubmit="getText"></translateForm>
        <!-- <translateOutput v-bind:translatedBack='translatedBack'></translateOutput> -->
        <translateOutput v-text='translatedBack'></translateOutput>
    </div>
</template>

<script>
/* eslint-disable */ 
import translateForm from './components/translateForm'
import translateOutput from './components/translateOutput'
export default {
    name: 'App', //只有作为组件选项(app.vue)时起作用,允许组件模板递归地调用自身,便于调试。有名字的组件有更友好的警告信息
    data(){
        return{
            translatedBack:''
        }
    },
    components:{   
        translateForm,
        translateOutput 
    },
    methods:{
        getText(text,language){ 

            this.$http.get('https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20180620T082602Z.f26ac3ff1eb746d8.60a3fd0f3de6f73d249c434f175f51d8eda029e7&lang='+language+'&text='+text)
                .then((res) =>{
                    console.log(res.body.text[0]);
                    this.translatedBack = res.body.text[0];
                })
        }
    }

}
</script>

<style>
    body{padding-top:100px;}
    #app{text-align: center;}
    h5,h1{text-align: center;margin-top:20px;}
    form{ margin:30px auto;}
    .form-inline{justify-content:space-between;}
    input[type='text']{width:66%!important;}
</style>



