<template>
    <div class="container">
        <div class="row">
            <div class="col-md-3"></div>
            <div class="col-md-6">
                <form class="form-inline well well-lg" @submit.prevent="formSubmit">
                    <input class="form-control" type="text" v-model="translateText" placeholder="请输入翻译内容">
                    <select  class="form-control" v-model="language">
                        <option value="en">English</option>
                        <option value="ru">Russian</option>
                        <option value="ko">Korean</option>
                        <option value="ja">Japanese</option>
                    </select> 
                    <input class="btn btn-primary" type="submit" value="翻译">
                </form>
            </div>
            <div class="col-md-3"></div>
        </div>
     </div>
</template>
<script>
/* eslint-disable */
 // eslint-disable-next-line
export default {
    name: 'translateForm',
    data(){
        return{
            translateText:'',
            language:'ko',
        }  
    },
    methods:{
        formSubmit(){
            this.$emit('formSubmit',this.translateText,this.language);  
        }
    }


}
</script>
<style>
</style>



